
import {computed} from "vue";
import {useStore} from "vuex";

export default {
  name: "NavBar",
  setup() {
    const store = useStore();

    const auth = computed(() => store.state.authenticated);

    const logout = async () => {
      await fetch('http://localhost:8000/api/logout', {
        method: 'POST',
        headers: {'Content-Type': 'application/json'},
        credentials: 'include'
      });
    }

    return {
      auth,
      logout
    }
  }
}
